.cart-dropdown {
  position: absolute;
  width: 240px;
  height: 340px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: 1px solid black;
  background-color: white;
  top: 90px;
  right: 40px;
  z-index: 5;
  opacity: 1;
  visibility: visible;
  backface-visibility: hidden;
  transition: height .5s, opacity .3s, visibility .3s;

  &.hidden {
    visibility: hidden;
    opacity: 0;
    height: 0;
  }

  .cart-items {
    height: 240px;
    display: flex;
    flex-direction: column;
    overflow: scroll;
  }

  button {
    margin-top: auto;
  }

  .empty-message {
    font-size: 18px;
    margin: 50px auto;
  }
}
